import React, { useState, createRef } from "react";
import {
  Button,
  Card,
  CardBody,
  CardGroup,
  Col,
  Container,
  Form,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Row
} from "reactstrap";
import { Redirect } from "react-router-dom";
import Axios from "axios";
import Cookies from "js-cookie";
import { Spinner } from "../../../component";

import history from "../../../history";

const Login = props => {
  const [isLoading, setIsLoading] = useState(false);
  const [formValue, setFormValue] = useState({
    username: createRef(),
    password: createRef()
  });

  var content = Cookies.get("admin_token") ? (
    <Redirect to="/admin/dashboard" />
  ) : (
    <div className="app flex-row align-items-center">
      <Container>
        <Spinner isLoading={isLoading} />
        <Row className="justify-content-center">
          <Col md="6">
            <CardGroup>
              <Card className="p-4">
                <CardBody>
                  <Form>
                    <h1>Login</h1>
                    <p className="text-muted">Sign In to your account</p>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-user"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input
                        type="text"
                        placeholder="Username"
                        autoComplete="username"
                        innerRef={formValue.username}
                      />
                    </InputGroup>
                    <InputGroup className="mb-4">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input
                        type="password"
                        placeholder="Password"
                        autoComplete="current-password"
                        innerRef={formValue.password}
                      />
                    </InputGroup>
                    <Row>
                      <Col xs="6">
                        <Button
                          color="primary"
                          className="px-4"
                          onClick={() => {
                            function log() {
                              setIsLoading(true);
                              Axios.post(
                                "https://antarblanja.com/api/admin/credential/login",
                                {
                                  username: formValue.username.current.value,
                                  password: formValue.password.current.value
                                }
                              )
                                .then(data => {
                                  if (data.data.token) {
                                    // login(data.data.token);
                                    Cookies.set("admin_token", data.data.token);
                                    history.push("/admin/dashboard");
                                  }
                                  setIsLoading(false);
                                })
                                .catch(e => {
                                  alert("login failed");
                                  setIsLoading(false);
                                });
                            }
                            log();
                          }}
                        >
                          Login
                        </Button>
                      </Col>
                      {/* <Col xs="6" className="text-right">
                      <Button color="link" className="px-0">
                        Forgot password?
                      </Button>
                    </Col> */}
                    </Row>
                  </Form>
                </CardBody>
              </Card>
            </CardGroup>
          </Col>
        </Row>
      </Container>
    </div>
  );
  return content;
};

export default Login;
